.login-container {
  max-width: 360px;
  margin: 0 auto;
  padding: 20px;
  /* font-family: "Arial", sans-serif; */
  text-align: center;
  min-height: 100%;
  display: flex;
  flex-direction: column;
}

.circle-logo {
  width: 120px;
  height: 120px;
  background-color: #008a69;
  border-radius: 50%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loginlogo-section {
  padding-top: 60px;
  /* margin-bottom: 10px; */
}

.kangaroo-img {
  width: 120px;
  height: auto;
}

.login-section {
  margin-top: 20px;
}

.login-title {
  font-size: 19px;
  font-weight: bold;
  margin-bottom: 20px;
  line-height: 1.4; /* Í∏∞Î≥∏ÏùÄ 1.2~1.4, Îçî ÌÅ¨Í≤å Ï°∞Ï†à Í∞ÄÎä• */

  /* (ÏÑ†ÌÉù) Í∏ÄÏûê Í∞ÑÍ≤©ÎèÑ ÏÇ¥Ïßù ÎäòÎ¶¨Í≥† Ïã∂Îã§Î©¥ */
  /* letter-spacing: 0.5px; */
}

.welcome-sub {
  font-size: 13px;
  color: #555;
  margin-bottom: 20px;
}

.login-form {
  margin-top: 20px;
}
.login-form input {
  width: 100%;
  padding: 12px;
  margin: 8px 0;
  border: none;
  border-bottom: 1px solid #ccc;
  outline: none;
}

.login-btn {
  width: 100%;
  padding: 14px;
  margin-top: 15px;
  background-color: #008a69;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
}

.login-btn:hover {
  background-color: #327255;
}

.options {
  margin-top: 20px;
  font-size: 13px;
  color: #444;
}

.options span {
  margin: 0 8px;
  color: #ccc;
}

.signup-link {
  color: #008a69;
  font-weight: bold;
}

