.search-container {
  display: flex;
  align-items: center;
  position: relative;
  width: 100%;
  padding: 0 20px;
  margin-top: 20px;
  box-sizing: border-box;
}

.search-input {
  width: 100%;
  height: 44px;
  padding: 0 45px 0 15px;
  border: none;
  border-radius: 22px;
  background-color: #ffffff;
  font-size: 14px;
  color: #333;

  /* Í∏∞Î≥∏ Í∑∏Î¶ºÏûê (Í∞ÄÎ≥çÍ≤å) */
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  transition: all 0.25s ease;
}

.search-input:focus {
  outline: none;
  /* hover/focus Í∑∏Î¶ºÏûê ‚Üí Ï°∞Í∏àÎßå ÏßÑÌïòÍ≤å */
  box-shadow: 0 3px 8px rgba(0, 138, 105, 0.25);
}

.search-input::placeholder {
  color: #aaa;
  font-size: 13px;
}

/* Í≤ÄÏÉâ Î≤ÑÌäº (ÎèãÎ≥¥Í∏∞) */
.search-icon {
  position: absolute;
  right: 35px;
  font-size: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;

  background: none;
  border: none;
}

.search-icon img {
  width: 18px;
  height: 18px;
  filter: brightness(0) saturate(100%) invert(37%) sepia(96%) saturate(454%)
    hue-rotate(122deg) brightness(92%) contrast(95%);
  /* ÏïÑÏù¥ÏΩòÏùÑ Footer Ï¥àÎ°ùÏÉâ ÌÜ§Í≥º Ïñ¥Ïö∏Î¶¨Í≤å */
}

/* X Î≤ÑÌäº (Í≤ÄÏÉâÏñ¥ Ï¥àÍ∏∞Ìôî) */
.clear-btn {
  position: absolute;
  right: 65px;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  border: none;
  background-color: #008a69; /* Footer Ï¥àÎ°ù */
  color: white;
  font-size: 11px;
  line-height: 1;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 6px rgba(0, 138, 105, 0.3);
  transition: background 0.2s;
}

.clear-btn:hover {
  background-color: #006f54;
}
