/* Î£®Ìä∏: Î∂ÄÎ™®(.app) ÏòÅÏó≠ ÏïàÏóêÏÑúÎßå ÍΩâ Ï±ÑÏö∞Í∏∞ */
.obRoot {
  position: relative; /* fixed Ï†úÍ±∞ */
  inset: auto; /* Ï¥àÍ∏∞Ìôî */
  width: 100%;
  height: 100%; /* Î∂ÄÎ™® ÎÜíÏù¥ Ï±ÑÏö∞Í∏∞ */
  min-height: 100%;
  padding: calc(24px + env(safe-area-inset-top, 0px)) 16px
    calc(16px + env(safe-area-inset-bottom, 0px)) 16px;
  background: #fff;
  display: flex;
  flex-direction: column;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  overflow: hidden; /* ÎÇ¥Î∂Ä ÏÑπÏÖòÏúºÎ°ú Ïä§ÌÅ¨Î°§ Í¥ÄÎ¶¨ */
}

/* Ïä§ÌÅ¨Î¶∞Ïù¥ ÏïÑÏ£º ÏûëÏùÑ ÎïåÎäî Ïó¨Î∞± Ï°∞Í∏à Ï§ÑÏù¥Í∏∞ */
@media (max-height: 640px) {
  .obRoot {
    padding-top: calc(16px + env(safe-area-inset-top, 0px));
    padding-bottom: calc(12px + env(safe-area-inset-bottom, 0px));
  }
}

/* ÎèÑÌä∏ */
.obDots {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin: clamp(16px, 5vh, 32px) 0 12px;
  margin-top: 90px;
}
.dot {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #d9d9d9;
  transition: transform 0.25s, background 0.25s;
  cursor: pointer;
}
.active {
  background: #008a69;
  transform: scale(1.2);
}

/* ÏïÑÌä∏ Ïª®ÌÖåÏù¥ÎÑà: ÎÜíÏù¥ Ï§ÑÏó¨ÏÑú Í≥ºÎåÄÎπÑ ÎÖ∏Ï∂ú Î∞©ÏßÄ */
.obArt {
  position: relative;
  height: clamp(32%, 36vh, 42%); /* Ïù¥Ï†Ñ 40% ‚Üí Í∞ÄÎ≥Ä */
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 30px;
  overflow: hidden; /* Ïù¥ÎØ∏ÏßÄ Ìäê Î∞©ÏßÄ */
}

/* Î∞∞Í≤Ω Ïù¥ÎØ∏ÏßÄ */
.obBg {
  position: absolute;
  inset: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.2; /* ÏÇ¥Ïßù Îçî Î∂ÄÎìúÎüΩÍ≤å */
  z-index: 0;
  pointer-events: none;
}

/* Ïù∏Î¨º ÏùºÎü¨Ïä§Ìä∏ */
.obAction {
  position: relative;
  z-index: 1;
  width: auto;
  height: 100%; /* Ïù¥Ï†Ñ 90% ‚Üí Ï∂ïÏÜå */
  max-height: 320px; /* ÏïÑÏ£º ÌÅ∞ ÌôîÎ©¥ÏóêÏÑúÎèÑ Í≥ºÎåÄ Î∞©ÏßÄ */
  object-fit: contain;
  object-position: center 65%;
  transform: translateY(6%); /* Ïù¥Ï†Ñ 10% ‚Üí ÏôÑÌôî */
  image-rendering: -webkit-optimize-contrast;
  backface-visibility: hidden;
}

/* ÌÖçÏä§Ìä∏ ÏòÅÏó≠ */
.obCopy {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 6px 4px 0;
  /* Í∏¥ Î≤àÏó≠/Ï§ÑÎ∞îÍøà ÎåÄÎπÑÌï¥ ÏïÑÎûò Ïó¨Î∞± ÌôïÎ≥¥ */
}

/* ÌÉÄÏù¥ÌãÄ: ÌÅ¨Í∏∞ Í∞ÄÎ≥Ä + ÏûêÍ∞Ñ/ÌñâÍ∞Ñ ÏïàÏ†ïÌôî */
.obTitle {
  white-space: pre-line;
  font-size: clamp(16px, 2.8vw, 18px); /* Ïù¥Ï†Ñ 20px ‚Üí Ï∂ïÏÜå + Í∞ÄÎ≥Ä */
  font-weight: 800;
  line-height: 1.45;
  letter-spacing: -0.2px;
  color: #222;
  margin: 12px 0 6px;
  text-align: center;
}

/* Í∞ïÏ°∞ */
.obHighlight {
  font-size: clamp(15px, 2.6vw, 17px);
  font-weight: 700;
  line-height: 1.4;
  color: #008a69;
  margin: 2px 0 0;
  text-align: center;
}

/* Í±¥ÎÑàÎõ∞Í∏∞ */
.obSkipCenter {
  display: block;
  width: auto;
  margin: 50px auto 0;
  background: transparent;
  border: none;
  color: #6a8b7c;
  font-size: 14px; /* 14 ‚Üí 13 */
  font-weight: 600;
  cursor: pointer;
}

/* ÌïòÎã® Î≤ÑÌäº ÏòÅÏó≠ */
/* ÏΩòÌÖêÏ∏†Í∞Ä Í∏∏Ïñ¥ÏßÄÎ©¥ Î≤ÑÌäºÏùÄ ÌïòÎã® Í≥†Ï†ï ÎäêÎÇåÏúºÎ°ú ÎÇ®Í∏∞Í≥†, 
   ÏûêÏ≤¥ ÎÇ¥Î∂Ä Ïä§ÌÅ¨Î°§Ïù¥ ÌïÑÏöîÌï† ÎïåÎ•º ÎåÄÎπÑÌï¥ Ïó¨Î∞± Ï°∞Ï†ï */
.obNav {
  margin-top: auto; /* ÏïÑÎûòÎ°ú Î∞ÄÏ∞© */
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 10px;
  padding-bottom: calc(env(safe-area-inset-bottom, 0px) + 110px);
  gap: 10px;
}

.obNavSingle {
  justify-content: center;
}

.obNavTwo {
  flex-direction: column;
  align-items: stretch;
  gap: 8px;
}

/* Î≤ÑÌäº */
.obLogin,
.obSignup {
  display: block;
  padding: 13px 16px; /* 10x18 ‚Üí ÏÇ¥Ïßù Ïª¥Ìå©Ìä∏ */
  font-size: 14px; /* 15 ‚Üí 14 */
  font-weight: 800;
  border-radius: 10px; /* 20 ‚Üí 18 */
  cursor: pointer;

  width: 90%;
  max-width: 300px;
  margin: 0 auto;

  border: none;
  outline: none;
  transition: transform 0.06s ease;
}

.obLogin {
  background: #008a69;
  color: #fff;
}
.obSignup {
  background: #fff;
  color: #008a69;
  border: 1px solid #008a69;
}

.obLogin:active,
.obSignup:active {
  transform: scale(0.985);
}

/* ÏûëÏùÄ ÌôîÎ©¥(ÏÑ∏Î°ú ÏßßÏùå) ÌäúÎãù */
@media (max-height: 640px) {
  .obArt {
    height: 34vh;
  }
  .obAction {
    height: 72%;
    max-height: 280px;
  }
  .obTitle {
    font-size: clamp(15px, 2.6vw, 17px);
  }
  .obHighlight {
    font-size: clamp(14px, 2.4vw, 16px);
  }
}

/* ÏïÑÏ£º ÌÅ∞ ÌôîÎ©¥ÏóêÏÑúÎèÑ ÎπÑÏú® Í≥ºÎåÄ Î∞©ÏßÄ */
@media (min-height: 920px) {
  .obArt {
    height: 38%;
  }
  .obAction {
    max-height: 360px;
  }
}

/* Î™®ÏÖò ÏÑ†Ìò∏ÌïòÏßÄ ÏïäÎäî ÏÇ¨Ïö©Ïûê */
@media (prefers-reduced-motion: reduce) {
  .dot,
  .active,
  .obLogin,
  .obSignup {
    transition: none !important;
  }
}
