  background: transparent;
  color: #9ca3af;
  font-size: 18px; /* ÌÖçÏä§Ìä∏Îäî Ï†úÍ±∞, ÌôîÏÇ¥ÌëúÎßå Î≥¥Ïù¥Í≤å */
  cursor: pointer;
}

/* Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Ïª®ÌÖåÏù¥ÎÑà */
.tmc-scroll {
  display: flex;
  gap: 12px;
  overflow-x: auto;
  padding-bottom: 10px;
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE/Edge */
  user-select: none;
}
.tmc-scroll::-webkit-scrollbar {
  display: none; /* Chrome/Safari */
}

/* ÎìúÎûòÍ∑∏ Ïãú ÏÇ¥Ïßù Í∞ïÏ°∞ */
.tmc-scroll.active {
