.signup-complete-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 40px 20px;
  text-align: center;
}

/* ÏÉÅÎã® ÏòÅÏó≠ (Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨) */
.complete-top {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; /* ÏÑ∏Î°ú Í∞ÄÏö¥Îç∞ */
  gap: 20px;
}

.check-circle {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  background: #008a69;
  display: flex;
  align-items: center;
  justify-content: center;
}

.check-icon {
  width: 45px;
  height: 45px;
}

.welcome-title {
  font-size: 20px;
  font-weight: 800;
  /* color: #008a69; */
  margin: 0;
}

.description {
  font-size: 15px;
  line-height: 1.5;
  color: #444;
  margin: 0;
}

