.desert-wrapper {
  margin-top: 16px;
  padding: 0 16px;
}

.desert-title {
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 12px;
  color: #333;
}

.highlight {
  color: #76a37c; /* Ìè¨Ïù∏Ìä∏ Ïª¨Îü¨ */
}

.desert-card {
  display: flex;
  align-items: center;
  background: #ffffff;
  padding: 16px;
  border-radius: 16px;

  /* ‚úÖ ÌÖåÎëêÎ¶¨ + Í∑∏Î¶ºÏûê */
  /* border: 1.5px solid #e3f3d9; Ïó∞Ìïú ÌååÏä§ÌÖî Í∑∏Î¶∞ */
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.08);

  transition: box-shadow 0.2s ease, transform 0.2s ease;
}

.desert-img {
  width: 56px;
  height: 56px;
  border-radius: 50%; /* ÏõêÌòï */
  background: #e3f3d9; /* ÏïÑÏù¥ÏΩò Î∞∞Í≤ΩÎèÑ Í∞ôÏùÄ Í≥ÑÏó¥ */
  padding: 10px; /* ÏïÑÏù¥ÏΩò Ïó¨Î∞± */
  object-fit: contain;
  margin-right: 14px;
  flex-shrink: 0;
}

.desert-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 3px;
}

.desert-name {
  font-size: 16px;
  font-weight: 600;
  color: #2c2c2c;
  margin-bottom: 4px;
}

.desert-link,
.desert-info {
  font-size: 13px;
  color: #666;
  display: inline-flex;
  align-items: center;
  gap: 4px;
  text-decoration: none;
  transition: color 0.2s ease;
}

.desert-link:hover {
  color: #008a69;
  text-decoration: underline;
}

.desert-info {
  color: #aaa; /* ÏùºÎ∞ò ÏùåÏãùÏùº Í≤ΩÏö∞ ÏòÖÏùÄ ÏÉâ */
}
