.recipe-card-container {
  width: 160px; /* Ï∫êÎü¨ÏÖÄ ÎÇ¥ ÏïÑÏù¥ÌÖú ÎÑàÎπÑ */
  flex-shrink: 0;
  min-height: 233px; /* ‚úÖ Ïπ¥Îìú ÎÜíÏù¥ Í≥†Ï†ï */
  max-height: 233px;
  background-color: #fff;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.2s ease-in-out;
  cursor: pointer;
  margin-top: 4px;
}

.recipe-card-container:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

.recipe-image-wrapper {
  width: 100%;
  height: 150px;
  overflow: hidden;
}

.recipe-image-wrapper img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.recipe-info {
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.recipe-meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5px;
}

/* .recipe-tags {
  font-size: 11px;
  color: #888;
} */
.heart-icon {
  font-size: 16px;
  color: #ddd; /* Í∏∞Î≥∏ ÌïòÌä∏ ÏÉâÏÉÅ */
  cursor: pointer; /* ÌÅ¥Î¶≠ Í∞ÄÎä•ÌïòÎèÑÎ°ù Ïª§ÏÑú Î≥ÄÍ≤Ω */
  transition: color 0.2s; /* ÏÉâÏÉÅ Î≥ÄÍ≤Ω Ïãú Î∂ÄÎìúÎüΩÍ≤å */
}

/* ÌïòÌä∏Í∞Ä 'Ï¢ãÏïÑÏöî' ÏÉÅÌÉúÏùº ÎïåÏùò ÏÉâÏÉÅ */
.heart-icon.liked {
  color: #ff6b6b; /* Îπ®Í∞ÑÏÉâÏúºÎ°ú Î≥ÄÍ≤Ω */
}

.recipe-name {
  font-size: 15px;
  font-weight: bold;
  color: #000000;
  margin: 0;
  text-align: left;
}

.highlight {
  color: #008a69 !important; /* Ï¥àÎ°ùÏÉâ */
  font-weight: 700;
}

.recipe-meta-text {
  font-size: 12px;
  color: #666;
}

.no-image {
  width: 100%;
  height: 150px; /* .recipe-image-wrapperÏôÄ ÎèôÏùºÌïú ÎÜíÏù¥ */
  display: flex;
  align-items: center;
  justify-content: center;
  background: linear-gradient(135deg, #f0f0f0, #e0e0e0);
  color: #666;
  font-size: 14px;
  font-weight: 500;
  border-radius: 8px;
}
