/* ===========================
   Í≤ÄÏÉâ ÏòÅÏó≠ (input + dropdown Ìïú Îç©Ïñ¥Î¶¨)
   =========================== */
.searchWrap {
  width: 100%;
  max-width: clamp(360px, 92vw, 560px);
  margin: 20px auto 20px;
  position: relative; /* Î≤ÑÌäº/ÎìúÎ°≠Îã§Ïö¥ Í∏∞Ï§Ä */
  z-index: 20; /* ÏïÑÎûò Ïπ¥ÎìúÎì§ ÏúÑÎ°ú */
}

/* inputÏù¥ Ïª®ÌÖåÏù¥ÎÑà Î∞ñÏúºÎ°ú Ïª§ÏßÄÏßÄ ÏïäÎèÑÎ°ù */
.searchInput {
  width: 100%;
  box-sizing: border-box;
  display: block;
  height: 44px;
  border: 1.5px solid #c7c7c7;
  border-radius: 22px;
  padding: 0 44px 0 16px; /* Ïò§Î•∏Ï™Ω ÎèãÎ≥¥Í∏∞ Í≥µÍ∞Ñ */
  font-size: 14px;
  outline: none;
  transition: border-color 0.2s ease, box-shadow 0.2s ease,
    border-radius 0.15s ease;
  background: #fff;
}
.searchInput:focus {
  border-color: #008a69;
  box-shadow: 0 0 0 4px rgba(0, 138, 105, 0.1);
}

/* focus/Í≤ÄÏÉâÏñ¥ ÏûÖÎ†•ÏúºÎ°ú ÎìúÎ°≠Îã§Ïö¥ Ïó¥Î¶¥ Îïå input ÏïÑÎûò ÎùºÏö¥Îìú Ï†úÍ±∞ + ÏïΩÍ∞Ñ Í∞ïÏ°∞ */
.searchWrapOpen .searchInput {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-color: #008a69;
  box-shadow: 0 0 0 4px rgba(0, 138, 105, 0.08);
}

/* ÎèãÎ≥¥Í∏∞ Î≤ÑÌäº (ÏïÑÏù¥ÏΩòÏùÄ JSXÏóêÏÑú ÌöåÏÉâ/Ï¥àÎ°ù ÌÜ†Í∏Ä) */
.searchBtn {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 28px;
  height: 28px;
  border: none;
  background: transparent;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 21; /* input ÏúÑÎ°ú */
}
.iconImg {
  width: 18px;
  height: 18px;
  display: block;
}

/* ===== ÎìúÎ°≠Îã§Ïö¥: inputÍ≥º 'Îî± Î∂ôÍ≤å' (1px Í≤πÏπ®ÏúºÎ°ú Í≤ΩÍ≥ÑÏÑ† ÏûêÏó∞Ïä§ÎüΩÍ≤å) ===== */
.searchDropdown {
  position: absolute;
  left: 0;
  right: 0;
  top: 43px; /* ‚úÖ input(44px)Î≥¥Îã§ 1px ÏúÑÎ°ú Í≤πÏπòÍ≤å */
  background: #fff;
  border: 1.5px solid #c7c7c7;
  border-top: none; /* ÏúÑÏ™Ω Î≥¥Îçî Ï†úÍ±∞ -> Ìïú Îç©Ïñ¥Î¶¨ */
  border-bottom-left-radius: 22px; /* ÏïÑÎûòÎßå ÎùºÏö¥Îìú */
  border-bottom-right-radius: 22px;
  box-shadow: 0 8px 18px rgba(0, 0, 0, 0.06);
  z-index: 30; /* Ïπ¥Îìú/Ïπ¥ÌÖåÍ≥†Î¶¨Î≥¥Îã§ ÏúÑ */
  max-height: 240px;
  overflow-y: auto;
}

/* ÎìúÎ°≠Îã§Ïö¥ Ïó¥Î¶º Ïãú, Í≤ΩÍ≥ÑÏÉâÏùÑ inputÍ≥º ÎßûÏ∂∞ Îçî ÏûêÏó∞Ïä§ÎüΩÍ≤å */
.searchWrapOpen .searchDropdown {
  border-color: #008a69;
}

/* Ï†úÏïà Î¶¨Ïä§Ìä∏ */
.suggestionList {
  list-style: none;
  margin: 0;
  padding: 8px 0;
}
.suggestionItem {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 10px 14px;
  font-size: 14px;
  background: transparent;
  border: none;
  text-align: left;
  cursor: pointer;
}
.suggestionItem:hover {
  background: #f5f5f5;
}
.suggestionName {
  font-weight: 600;
}
.suggestionCat {
  font-size: 12px;
  color: #777;
}

/* ===== Í≤ÄÏÉâ Í≤∞Í≥º ÏóÜÏùå: ÌÅºÏßÅÌïòÍ≥† Ï§ëÏïôÏ†ïÎ†¨ ===== */
.noResultBox {
  padding: 40px 20px;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.noResultIconImg {
  width: 72px;
  height: 72px;
  display: block;
  margin: 0 0 16px;
  opacity: 0.7;
}
.noResultText {
  font-size: 15px;
  color: #555;
  line-height: 1.6;
  font-weight: 500;
}

/* ===========================
   Ïπ¥ÌÖåÍ≥†Î¶¨ Ïπ¥Îìú
   =========================== */
.categoryStack {
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin-top: 12px;
  z-index: 5;
}
.categoryCard {
  border: 1px solid #e5e5e5;
  border-radius: 12px;
  padding: 16px;
  background: #fafafa;
}
.categoryHeader {
  margin-bottom: 12px;
}
.categoryBadge {
  display: inline-block;
  padding: 6px 12px;
  border-radius: 9999px;
  background: #008a69;
  color: #fff;
  font-size: 15px;
  font-weight: 700;
  line-height: 1;
}
.pillGrid {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.pillBtn {
  min-width: 90px;
  padding: 8px 14px;
  font-size: 14px;
  border: 1.5px solid #c7c7c7;
  border-radius: 22px;
  background: #ffffff;
  cursor: pointer;
  transition: all 0.15s ease;
}
.pillBtn:hover {
  border-color: #777;
}
.pillSelected {
  border-color: #008a69;
  background: #baf8e9;
  color: #008a69;
  font-weight: 600;
}

/* ===========================
   Í∏∞ÌÉÄ ÏûÖÎ†•: ÌïòÎã® Í≥†Ï†ï + Í≥µÌÜµ Ìè≠
   =========================== */
.etcSection {
  position: static;
  left: 0;
  bottom: 96px; /* ÌïòÎã® Î≤ÑÌäº Î∞îÎ°ú ÏúÑ */
  width: 100%;
  background: #fff;
  z-index: 25; /* ÌïòÎã® Î≤ÑÌäºÎ≥¥Îã§ ÏúÑ */

  padding: 12px 0;
  max-height: 400px; /* ÏµúÎåÄ ÎÜíÏù¥ ÏÑ§Ï†ï */
  overflow-y: auto; /* Ïä§ÌÅ¨Î°§ Í∞ÄÎä•ÌïòÍ≤å ÏÑ§Ï†ï */
}
.etcInner {
  max-width: clamp(360px, 92vw, 560px);
  margin: 0 auto;
  padding: 0 20px;
}
.etcHeader {
  font-weight: 700;
  margin-bottom: 8px;
}
.etcRow {
  display: flex;
  align-items: center;
  gap: 12px;
}
.etcInput {
  flex: 1;
  min-width: 0;
  height: 44px;
  border: 1.5px solid #c7c7c7;
  border-radius: 22px;
  padding: 0 14px;
  font-size: 14px;
  outline: none;
  transition: border-color 0.2s ease, box-shadow 0.2s ease;
  box-sizing: border-box;
  display: block;
}
.etcInput:focus {
  border-color: #008a69;
  box-shadow: 0 0 0 4px rgba(0, 138, 105, 0.1);
}
.etcAddBtn {
  flex: 0 0 72px;
  height: 44px;
  border-radius: 9999px;
  border: 2px solid #008a69;
  background: #008a69;
  color: #ffffff;
  font-size: 14px;
  font-weight: 700;
  cursor: pointer;
  transition: background 0.15s ease;
  text-align: center;
}
.etcAddBtn:hover {
  background: #00725a;
}
.othersChips {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}
.otherChip {
  background: #baf8e9;
  border-radius: 16px;
  padding: 6px 10px;
  font-size: 13px;
  display: flex;
  align-items: center;
  gap: 6px;
}
.otherChipX {
  border: none;
  background: transparent;
  font-size: 14px;
  cursor: pointer;
}

/* ===========================
   Î™®Îã¨
   =========================== */
.modalOverlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 50;
}
.modalCard {
  background: #fff;
  border-radius: 12px;
  padding: 20px;
  width: 90%;
  max-width: clamp(360px, 92vw, 560px);
  margin: 0 auto;
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.1);
}
.modalTitle {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 16px;
}
.modalSection {
  margin-bottom: 12px;
}
.modalLabel {
  font-weight: 600;
  margin-bottom: 6px;
}
.modalEmpty {
  font-size: 13px;
  color: #777;
}
.modalList {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
}
.modalPill {
  background: #f0f0f0;
  border-radius: 16px;
  padding: 6px 10px;
  font-size: 13px;
}

.modalActions {
  display: flex;
  justify-content: flex-end;
  gap: 12px;
  margin-top: 20px;
}
.modalCancel,
.modalSubmit {
  flex: 1 1 50%;
  height: 48px;
  border-radius: 9999px;
  font-weight: 800;
  font-size: 15px;
  cursor: pointer;
  border: 2px solid transparent;
}
.modalCancel {
  background: #fff;
  color: #008a69;
  border-color: #008a69;
}
.modalSubmit {
  background: #008a69;
  color: #fff;
  border-color: #008a69;
}
.modalSubmit:disabled {
  background: #c7c7c7;
  border-color: #c7c7c7;
  cursor: not-allowed;
}
