.meal-header {
  flex: 0 0 auto; /* ‚úÖ Ìó§ÎçîÎäî Í≥†Ï†ï ÎÜíÏù¥ ‚Üí flexÏóê ÌúòÎëòÎ¶¨ÏßÄ ÏïäÏùå */
  position: relative;
  display: flex;
  justify-content: center; /* labelÏùÄ Í∞ÄÏö¥Îç∞ */
  align-items: center;
  padding: 15px 16px;
  /* border-bottom: 1px solid #ddd; */
}

.meal-header::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 90%; /* ‚úÖ ÏõêÌïòÎäî Í∏∏Ïù¥ (Ïòà: 60%) */
  border-bottom: 1px solid #ddd;
}

.meal-label {
  text-align: center;
  font-weight: 750;
}

.meal-icon {
  margin-left: 4px;
}

.refresh-btn {
  position: absolute;
  right: 16px;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 14px;
  color: #666;
}

.refresh-btn:hover {
  color: #222;
}
