.p-grid {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 24px 20px; /* ÌôîÎ©¥ Í∞ÄÏû•ÏûêÎ¶¨ Ïó¨Î∞± */
  gap: 14px; /* Î≤ÑÌäº ÏÇ¨Ïù¥ Í∞ÑÍ≤© */
}

/* Î≤ÑÌäº Ïπ¥Îìú */
.p-card {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 18px 20px;
  background: #fff;
  border: 1px solid #e5e5ea; /* ÏòÖÏùÄ ÌöåÏÉâ ÌÖåÎëêÎ¶¨ */
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.25);
  border-radius: 14px;
  cursor: pointer;
  position: relative;
  transition: background 0.2s ease, transform 0.08s ease;
}

.p-card:hover {
  background: #e6e9e6;
}
.p-card:active {
  background: #f2f2f7;
  transform: scale(0.99);
}

/* Ï†úÎ™© */
.p-card-title {
