/* === statuscard.css === */
.pg-card {
  background: #ffffff;
  padding: 16px;
  border-radius: var(--card-radius, 16px);
  /* box-shadow: var(--shadow, 0 4px 16px rgba(0, 0, 0, 0.08)); */
  /* box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25); */
  width: 93%;
  box-sizing: border-box;
  /* border-radius: 10; */
  display: grid;
  grid-template-columns: 1fr 1px 1fr; /* ÏôºÏ™Ω | Íµ¨Î∂ÑÏÑ† | Ïò§Î•∏Ï™Ω */
  column-gap: 16px;
  align-items: center;
  margin-bottom: 30px;
  margin: -50px auto 20px; /* ‚¨ÖÔ∏è ÏúÑÎ°ú Í≤πÏπòÍ≤å (-40px) */
  position: relative;
  z-index: 2; /* Ìó§Îçî ÏúÑÎ°ú Ïò¨ÎùºÏò§ÎèÑÎ°ù */

  /* ÏúÑ/ÏïÑÎûòÎßå ÏùÄÏùÄÌïòÍ≤å ÌçºÏßÄÎäî Í∑∏Î¶ºÏûê */
  box-shadow: 0 -2px 3px -1px rgba(0, 0, 0, 0.05),
    0 6px 12px -2px rgba(0, 0, 0, 0.08);
}

.pg-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  gap: 10px;
  min-width: 0;
}

.pg-label {
  font-size: 14px;
  font-weight: 800;
  color: #222;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  /* margin-left: 16px; */
}

/* ===== D-Day Î±ÉÏßÄ (ÏôºÏ™Ω) ===== */
.pg-dday {
  appearance: none;
  border: 0;
  background: #fcfcfc; /* ÏÇ¥Ïßù Ïπ¥Îìú ÎäêÎÇå */
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.05);
  padding: 10px 12px;
  min-width: 120px;
  display: inline-flex;
  flex-direction: column;
  align-items: center;
  gap: px;
  cursor: pointer;
}
.pg-dday:active {
  transform: translateY(0.5px);
}
.pg-dday-label {
  font-size: 12.5px;
  color: #6b7280;
}
.pg-dday-value {
  font-size: 20px;
  font-weight: 800;
  letter-spacing: 0.5px;
}

/* ===== Î∞òÏõê Í≤åÏù¥ÏßÄ (Ïò§Î•∏Ï™Ω) ===== */
.pg-gauge {
  width: 120px;
  height: 64px;
  margin: 0 auto;
}
.pg-svg {
  display: block;
  width: 100%;
  height: auto;
}

/* Í∞í/ÏÑ§Î™Ö ÌÖçÏä§Ìä∏ */
.pg-metric {
  font-size: 13px;
  color: #111827;
}
.pg-unit {
  font-size: 12px;
  color: #666;
}

.pg-divider {
  width: 1px;
  align-self: stretch;
  background: #e5e7eb;
  opacity: 0.9;
}

/* Í∞í ÏóÜÏùÑ Îïå Í≥µÌÜµ Î≤ÑÌäº */
.pg-empty {
  appearance: none;
  border: 0;
  background: transparent;
  color: #666;
  font-size: 13px;
  display: inline-flex;
  align-items: center;
  gap: 6px;
  cursor: pointer;
  padding: 4px 7px;
  border-radius: 8px;
}
.pg-empty:hover {
  background: rgba(0, 0, 0, 0.04);
}

.plus {
  font-size: 18px;
  line-height: 1;
  color: #888;
}

/* ÏûëÏùÑ Îïå ÏÇ¥Ïßù Ï∂ïÏÜå */
@media (max-width: 360px) {
  .pg-gauge {
    width: 108px;
    height: 58px;
  }
  .pg-dday {
    min-width: 108px;
  }
}

.pg-gauge-value {
  text-anchor: middle; /* Í∞ÄÏö¥Îç∞ Ï†ïÎ†¨ */
  dominant-baseline: middle;
  font-size: 15px;
  font-weight: 800;
  fill: #111827; /* Í∏ÄÏûê ÏÉâÏÉÅ */
}

.pg-gauge-value tspan.unit {
  font-size: 11px;
  font-weight: 600;
  fill: #6b7280; /* Îã®ÏúÑÎäî ÌöåÏÉâ */
}

.pg-gauge-value:hover {
  fill: #008a69; /* hover Ïãú Í∏ÄÏûê ÏÉâÏÉÅ ÌååÎûë */
  cursor: pointer;
}
