/* src/components/calendar/calendar.css */

/* Ï∫òÎ¶∞Îçî ÎÖÑÎèÑ,Ïõî ÏÑ†ÌÉù */
.calendar-header {
  display: flex;
  align-items: center;
  gap: 4px;
  font-size: 14px;
  font-weight: 500;
  padding: 0 16px;
  margin-bottom: 12px;
}

.big-number {
  font-size: 15px;
  font-weight: 900;
  /* color: #333; */
}

.unit-text {
  font-size: 12px;
  font-weight: 600;
  /* color: #666; */
}

.calendar-header-button {
  border: none;
  background: none;
  font-size: 12px;
  cursor: pointer;
}

/* ÏÉÅÏÑ∏ Ï∫òÎ¶∞Îçî, Ï£º Î≥ÄÍ≤Ω */
.week-dates-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.nav-button {
  background: none;
  border: none;
  font-size: 12px;
  color: #686868;
  cursor: pointer;
  padding: 4px;
}

.week-dates {
  display: flex;
  justify-content: space-between;
  gap: 8px;
  text-align: center;
}

.date-item {
  width: 40px;
  cursor: pointer;
  font-weight: 600;
  padding: 10px 0; /* ÏÑ†ÌÉù ÏãúÏôÄ Ïïà ÏÑ†ÌÉù Ïãú ÎÜíÏù¥ ÏùºÏπò */
  border-radius: 30px;
  /* transition: all 0.01s ease; */
}

.day-name {
  font-size: 13px;
  color: #686868;
}

.day-number {
  margin-top: 10px;
  font-size: 14px;
  color: #333;
}

.date-item.sunday .day-number {
  color: red;
}

.date-item.saturday .day-number {
  color: #6666ff;
}

.date-item.selected {
  background-color: #008a69;
  border-radius: 30px;
  padding: 10px 0;
  color: white;
  font-weight: bold;
}

.date-item.selected .day-name,
.date-item.selected .day-number {
  color: white;
}

.date-item.disabled {
  color: #c0c0c0; /* ÌöåÏÉâ Í∏ÄÏî® */
  pointer-events: none; /* ÌÅ¥Î¶≠ ÎßâÍ∏∞ */
  opacity: 0.6;
}
