/* ===== Footer Ïª®ÌÖåÏù¥ÎÑà ===== */
.footer {
  position: absolute;
  inset-inline: 0;
  bottom: max(9px, env(safe-area-inset-bottom));
  width: min(96%, var(--app-max, 390px));
  margin: 0 auto;

  display: flex;
  justify-content: space-between;
  align-items: center;
  /* height: 65px; */
  height: var(--footer-h);
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
  padding: 0 12px;
  z-index: 5;
}

/* ===== Footer ÏïÑÏù¥ÌÖú ===== */
.footer-item {
  flex: 1 1 0;
  text-align: center;
  cursor: pointer;
  user-select: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: transform 0.25s ease, color 0.25s ease;
}

/* ÏïÑÏù¥ÏΩò Í≥µÌÜµ */
.footer-icon {
  width: 33px; /* Ïõê ÌÅ¨Í∏∞ Ï§ÑÏûÑ */
  height: 33px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;

  font-size: 22px; /* ÏïÑÏù¥ÏΩò ÌÅ¨Í∏∞ ÏÇ¥Ïßù ÌÇ§ÏõÄ */
  color: #6b7280;
  transition: all 0.2s ease-in-out;
}

/* ÏïÑÏù¥ÏΩò ÌôúÏÑ±Ìôî */
.footer-item.is-active .footer-icon {
  background-color: rgba(0, 138, 105, 0.15); /* Ïó∞Ìïú Ï¥àÎ°ù Î∞∞Í≤Ω */
  color: #008a69; /* ÏïÑÏù¥ÏΩò Ï¥àÎ°ù */
  /* ‚úÖ ÌÖåÎëêÎ¶¨ Ï†úÍ±∞ */
}

/* ÎùºÎ≤® */
.footer-label {
  font-size: 11px;
  font-weight: 600;
  margin-top: 2px; /* ÌÖçÏä§Ìä∏ Îçî Í∞ÄÍπåÏù¥ */
  color: #6b7280;
  /* transition: color 0.25s ease-in-out, transform 0.25s ease-in-out; */
}
.footer-item.is-active .footer-label {
  color: #008a69;
}

/* ===== Ï§ëÏïô FAB Î≤ÑÌäº ===== */
.footer-center {
  position: relative;
  top: -8px;
  background-color: #008a69;
  border-radius: 50%;
  box-shadow: 0 3px 10px rgba(0, 138, 105, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 60px; /* ‚úÖ Í∏∞Ï°¥ 70 ‚Üí Ï§ÑÏûÑ */
  height: 60px;
  flex: none;
  margin-inline: 8px;
}

.footer-center img {
  width: 42px; /* ‚úÖ Ïù¥ÎØ∏ÏßÄ ÌÅ¨Í≤å */
  height: 42px;
  object-fit: contain;
}
.footer-item:hover {
  transform: translateY(-2px); /* hover Ïãú ÏÇ¥Ïßù ÏúÑÎ°ú */
}
