/* ===========================
   Intro / Done ÌÜµÌï© Í≥µÌÜµ CSS
   =========================== */
:root {
  --cPrimary: #008a69; /* Î©îÏù∏ Ï≤≠Î°ù */
  --cPrimaryHover: #1aba95; /* Î≤ÑÌäº hover */
  --cAccent: #ff8000; /* Ï£ºÌô© Ìè¨Ïù∏Ìä∏ */
  --cAccentLight: #ffcd94; /* Ïó∞Ìïú Ï£ºÌô©(Î≥¥Ï°∞) */
  --cBg: #ffffff; /* Î∞∞Í≤Ω */
  --cSurface: #baf8e9; /* ÏÑ†ÌÉù ÏÑúÌîºÏä§ */
  --cBorder: #c7c7c7; /* ÌÖåÎëêÎ¶¨ */
  --cTextMain: #000000; /* Í∏∞Î≥∏ ÌÖçÏä§Ìä∏ */
  --cTextSub: #7c7c7c; /* Î≥¥Ï°∞ ÌÖçÏä§Ìä∏ */
}

/* Ïª®ÌÖåÏù¥ÎÑà */
.surveyContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;

  /* Î∞òÏùëÌòï Ìå®Îî© (ÏÉÅÎã®Ïó¨Î∞±: Î™®Î∞îÏùº~Îç∞Ïä§ÌÅ¨ÌÜ±) */
  padding: clamp(80px, 20vh, 160px) 16px 90px;
  box-sizing: border-box;
  background: var(--cBg);
  overflow: hidden;
  font-family: "Noto Sans KR", sans-serif;
  color: var(--cTextMain);

  --titleColor: var(--cAccent); /* Í∏∞Î≥∏: Ï£ºÌô©(Ïù∏Ìä∏Î°ú) */
  --subColor: var(--cPrimary); /* Í∏∞Î≥∏: Ï≤≠Î°ù */
}

/* ÏôÑÎ£å ÌôîÎ©¥ ÌÖåÎßà */
.done {
  --titleColor: var(--cPrimary);
  --subColor: var(--cPrimary);
}

/* Î∞∞Í≤Ω Ìå®ÌÑ¥ */
.bgWrapper {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 500px;
  height: 500px;
  margin-top: -220px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  z-index: 0;
  pointer-events: none;
}

/* ÏÉÅÎã® ÏΩòÌÖêÏ∏† Î¨∂Ïùå */
.topGroup {
  position: relative;
  z-index: 2;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Î©îÏù∏ ÏùºÎü¨Ïä§Ìä∏ */
.heroImg {
  max-width: 90%;
  height: auto;
  user-select: none;
  -webkit-user-drag: none;
  position: relative;
  z-index: 3;
}

/* Ï†úÎ™© / Î©îÏãúÏßÄ */
.titleMain {
  margin: -1px 0 12px;
  font-size: clamp(28px, 6vw, 25px);
  font-weight: 700;
  color: var(--titleColor);
  text-align: center;
  letter-spacing: -0.2px;
  line-height: 1.2; /* ‚úÖ ÏùåÏàò Ï†úÍ±∞ */
  position: relative;
  z-index: 3;
}
.titleSub {
  margin: 0;
  font-size: clamp(14px, 3.5vw, 16px);
  font-weight: 700;
  color: var(--subColor);
  text-align: center;
  line-height: 1.4;
  position: relative;
  z-index: 3;
}

/* ÏôÑÎ£å ÌôîÎ©¥ Î¨∏Íµ¨ */
.messageBox {
  text-align: center;
  line-height: 1.5;
}

/* CTA Î≤ÑÌäº */
.ctaBtn {
  width: 100%;
  max-width: 280px; /* ÏµúÎåÄ 400pxÎ°ú Ï†úÌïú */
  height: 48px;
  border: none;
  border-radius: 999px;
  background: var(--cPrimary);
  color: var(--cBg);
  font-size: 16px;
  font-weight: 700;
  letter-spacing: -0.2px;
  cursor: pointer;
  margin: 50px auto; /* Ï§ëÏïô Ï†ïÎ†¨ */
  box-shadow: 0 6px 16px rgba(0, 138, 105, 0.25);
  transition: transform 0.15s ease, filter 0.2s ease, background-color 0.2s ease;
  position: relative;
  z-index: 2;
  padding: 0 16px; /* ÌòπÏãú Í∏∏Ïù¥ ÏßßÏùÑ Í≤ΩÏö∞ Ïó¨Î∞± ÌôïÎ≥¥ */
}

.ctaBtn:hover {
  background: var(--cPrimaryHover);
  transform: scale(1.02);
}
.ctaBtn:active {
  transform: scale(0.98);
  filter: brightness(0.96);
}

@media (max-width: 480px) {
  .surveyContainer {
    padding: 60px 12px 48px;
  }

  .ctaBtn {
    max-width: 80%; /* Îçî ÎÑìÍ≤å Î≥¥Ïù¥ÎèÑÎ°ù */
  }
}
