/* ÏïàÎÇ¥ Î¨∏Íµ¨ */
.form-intro {
  font-size: 14px;
  color: #555;
  margin-bottom: 20px;
  text-align: left;
}

/* Ìèº */
.signup-form {
  display: flex;
  flex-direction: column;
  gap: 28px; /* ÏûÖÎ†• Í∑∏Î£π Í∞ÑÍ≤© */
  text-align: left;
}

/* ÎùºÎ≤® + Ïù∏Ìíã Î¨∂Ïùå */
.signup-form .form-group {
  display: flex;
  flex-direction: column;
  gap: 4px; /* ÎùºÎ≤®Í≥º Ïù∏ÌíãÏùÄ Í∞ÄÍπùÍ≤å */
}

.signup-form label {
  font-size: 14px;
  font-weight: bold;
  display: flex;
  align-items: center;
  gap: 4px;
}

/* Îπ®Í∞Ñ * */
.required {
  color: red;
  font-weight: bold;
  font-size: 14px;
}

.signup-form input {
  padding: 10px;
  border: 1.5px solid #dadfdd;
  border-radius: 6px;
  font-size: 14px;
  transition: border-color 0.25s ease;
  /* transition: border-color 0.25s ease, box-shadow 0.25s ease; */
}

/* ÏûÖÎ†• Ï§ëÏùº Îïå ÌÖåÎëêÎ¶¨ ÏÉâ + Í∑∏Î¶ºÏûê Í∞ïÏ°∞ */
.signup-form input:focus {
  border-color: #008a69; /* Ï¥àÎ°ùÏÉâ ÌÖåÎëêÎ¶¨ */
  /* box-shadow: 0 0 0 2px rgba(0, 138, 105, 0.2); ÏÇ¥Ïßù ÎπõÎÇòÎäî Ìö®Í≥º */
  outline: none; /* Í∏∞Î≥∏ ÌååÎûÄ Ïô∏Í≥ΩÏÑ† Ï†úÍ±∞ */
}

/* Î≤ÑÌäº */
.submit-btn {
  margin-top: 58px;
  background-color: #008a69;
  color: #fff;
  font-size: 16px;
  font-weight: bold;
  padding: 14px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  transition: background-color 0.25s ease;
}

.submit-btn:hover:enabled {
  background-color: #008a69;
}

.submit-btn:disabled {
  background-color: #b5dcd2;
  cursor: not-allowed;
}

/* ÎπÑÎ∞ÄÎ≤àÌò∏ ÌïÑÎìú Í∞êÏã∏Îäî ÎûòÌçº */
.password-wrapper {
  display: flex;
  align-items: center;
  position: relative;
}

.password-wrapper input {
  flex: 1;
  padding-right: 40px; /* ÏïÑÏù¥ÏΩò Í≥µÍ∞Ñ */
}

.toggle-password {
  position: absolute;
  right: 10px;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 18px;
  color: #bbbbbb;
  top: 50%; /* Ïù∏Ìíã ÏÑ∏Î°ú Í∞ÄÏö¥Îç∞ */
  transform: translateY(-50%); /* Ï†ïÏ§ëÏïô */
}
