.detail-step {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 12px;
  padding: 12px 28px;
  /* border-bottom: 1px solid #eee; */
}

.step-info {
  flex: 1;
}

.step-badge {
  display: inline-block;
  background: #76a37c;
  color: #fff;
  font-size: 12px;
  font-weight: 600;
  border-radius: 12px;
  padding: 4px 10px;
  margin-bottom: 6px;
}

.step-desc {
  font-size: 14px;
  color: #333;
  margin: 0;
  line-height: 1.4;
  max-width: 230px;
}

.step-image img {
  width: 80px;
  height: 80px;
  border-radius: 6px;
  object-fit: cover;
}

