/* Ìó§Îçî ÏïÑÎûò, Ïä§ÌÅ¨Î°§ ÏòÅÏó≠ Î∞ñ */
.cat-rail-wrap {
  position: relative;
  background: #f9fafe;
  /* border-bottom: 1px solid #eee; */
  border-bottom: none;
}

/* Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Ïä§Ìä∏Î¶Ω */
.cat-rail {
  display: flex;
  gap: 12px;
  padding: 12px 0 0;
  overflow-x: auto;
  overscroll-behavior-x: contain; /* ‚Üê Ïò§ÌÉÄ ÏàòÏ†ï */
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
  cursor: grab;

  border-bottom: 1px solid #d9d9d9; /* ÌöåÏÉâ ÎùºÏù∏ */
  position: relative; /* ‚¨ÖÔ∏è z-index Í∏∞Ï§Ä */
  z-index: 1; /* ‚¨ÖÔ∏è ÎÇ¥Ïö©/ÎùºÏù∏ÏùÑ ÌéòÏù¥Îìú ÏúÑÎ°ú */
}
.cat-rail::-webkit-scrollbar {
  display: none;
}
.cat-rail.dragging {
  cursor: grabbing;
}

/* Ïπ© Ìè≠ÏùÑ ÎÑâÎÑâÌûà ‚Üí Ïã§Ï†úÎ°ú ÎÑòÏπòÍ≤å */
.cat-pill {
  position: relative;
  flex: 0 0 auto;
  min-width: 72px; /* ‚Üê Ï§ëÏöî: ÎÑòÏπòÍ≤å ÎßåÎìúÎäî ÌïµÏã¨ */
  text-align: center;
  padding: 13px 10px;
  background: transparent;
  border: none;
  font-size: 14px;
  color: #8a8f95;
  white-space: nowrap;
  border-bottom: 2px solid transparent;
  cursor: pointer;
}

.cat-pill.is-active {
  color: #0a8a60;
  font-weight: 700;
  border-bottom-color: #0a8a60;
  border-bottom-width: 3px;
}

/* ÌéòÏù¥ÎìúÍ∞Ä ÎùºÏù∏/ÎÇ¥Ïö©ÏùÑ ÎçÆÏßÄ ÏïäÎèÑÎ°ù */
.fade {
  pointer-events: none;
  position: absolute;
  top: 0;
  bottom: 2px; /* ‚¨ÖÔ∏è Î∞ëÏ§Ñ Ïïà Í∞ÄÎ¶¨Í≤å ÏÇ¥Ïßù ÎùÑÏõÄ */
  width: 20px;
  z-index: 0; /* ‚¨ÖÔ∏è Î†àÏùº(ÏΩòÌÖêÏ∏†)Î≥¥Îã§ ÏïÑÎûò */
}
.fade.left {
  left: 0;
  background: linear-gradient(90deg, #fff 35%, rgba(255, 255, 255, 0));
}
.fade.right {
  right: 0;
  background: linear-gradient(270deg, #fff 35%, rgba(255, 255, 255, 0));
}

/* ‚úÖ ÏãúÍ∞ÑÎ≥Ñ(ÌÉ≠ 4Í∞ú)Ïùº Îïå Í∑†Îì± Î∞∞Ïπò */
.cat-rail.is-short {
  overflow-x: hidden;
  gap: 0;
  padding: 12px 0 0; /* Ï¢åÏö∞ Ìå®Îî© Ï†úÍ±∞ ‚Üí Ï†ÑÏ≤¥Î•º ÍΩâ Ï±ÑÏõÄ */
  justify-content: space-between;
}

.cat-rail.is-short .cat-pill {
  flex: 1 1 0; /* 4Îì±Î∂ÑÏúºÎ°ú Í∑†Îì± Î∂ÑÎ∞∞ */
  min-width: 0;
  text-align: center;
  padding: 13px 0; /* Ï¢åÏö∞ Ìå®Îî©ÎèÑ Ï†úÍ±∞ */
}

/* ‚úÖ Ï¥àÎ°ùÏÑ† = Î≤ÑÌäº Ìè≠ 100% */
.cat-rail.is-short .cat-pill.is-active {
  border-bottom: 3px solid #0a8a60; /* Î≤ÑÌäº Ï†ÑÏ≤¥ Ìè≠Ïóê ÎßûÏ∂∞ÏÑú ÎùºÏù∏ ÏÉùÏÑ± */
  font-weight: 700;
  color: #0a8a60;
}
