.meal-page {
  position: relative;
  background-color: #f9fafe;
  min-height: 100vh;
  overflow: hidden;
  margin-top: 16px; /* HeaderÏôÄ Calendar ÏÇ¨Ïù¥ Í∞ÑÍ≤© */
}

.meal-bg {
  position: absolute;
  top: 300px; /* Ìó§Îçî+Ï∫òÎ¶∞Îçî ÎÜíÏù¥Ïóê Îî∞Îùº Ï°∞Ï†à */
  left: 0;
  width: 100%;
  height: 100%;
  /* background-color: #faf4ec; Ïó∞Ìïú Î∞∞Í≤Ω */
  border-top-left-radius: 40px;
  border-top-right-radius: 40px;
  z-index: 0;
}

.meal-content {
  position: relative;
  z-index: 1;
  padding: 0 16px;
}
.no-meal-popup-backdrop {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.4); /* Î∞òÌà¨Î™Ö Î∞∞Í≤Ω */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.no-meal-popup {
  background: #fff;
  border-radius: 16px;
  padding: 24px 20px;
  text-align: center;
  width: 280px;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
}

.no-meal-popup svg {
  margin-bottom: 12px;
  color: #ededee; /* ÏòÖÏùÄ ÌöåÏÉâ ÏïÑÏù¥ÏΩò */
}

.no-meal-popup p {
  margin: 4px 0;
  font-size: 15px;
  color: #374151; /* ÏßÑÌïú ÌöåÏÉâ ÌÖçÏä§Ìä∏ */
}

.no-meal-popup button {
  margin-top: 16px;
  width: 100%;
  padding: 10px 0;
  background: #008a69; /* ‚úÖ Ïï± Ï¥àÎ°ùÏÉâ */
  color: #fff;
  font-weight: 700;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 15px;
  transition: background 0.2s;
}

.no-meal-popup button:hover {
  background: #059669; /* hover Ïãú Îçî ÏßÑÌïú Ï¥àÎ°ù */
}
