  overflow-x: auto; /* Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Í∞ÄÎä• */
  /* padding-bottom: 8px; */
}

/* Í≤∞Í≥º ÏûàÏùå */
.result-section {
  padding: 16px;
}

/* Ïó∞Í¥Ä ÌÉúÍ∑∏ */
.related-tags {
  display: flex;
  align-items: center;
  gap: 10px;
  margin-bottom: 16px;
