}

.tag-label {
  font-size: 14px;
  font-weight: 800;
  color: #000000;
  margin-right: 10px;
}

/* Ïó∞Í¥Ä ÌÉúÍ∑∏ Î≤ÑÌäº */
.tag-btn {
  background: #fff; /* Ïó∞Ìïú ÎØºÌä∏ Î∞∞Í≤Ω */
  border: 1px solid #008a69; /* Ïó∞Ìïú Ï¥àÎ°ù ÌÖåÎëêÎ¶¨ */
  border-radius: 18px;
  padding: 6px 14px;
  font-size: 13px;
  color: #008a69; /* Î©îÏù∏ Ïª¨Îü¨ ÌÖçÏä§Ìä∏ */
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}

.tag-btn:hover {
  background: rgba(0, 138, 105, 0.15); /* Ìò∏Î≤Ñ Ïãú Ï°∞Í∏à ÏßÑÌïú ÎØºÌä∏ */
  border-color: #008a69; /* ÌÖåÎëêÎ¶¨ÎèÑ ÏÇ¥Ïßù ÏßÑÌïòÍ≤å */
}

.tag-btn:active {
  background: #a7f3d0; /* ÎàåÎ†ÄÏùÑ Îïå Îçî ÏßÑÌïòÍ≤å */
  transform: scale(0.96); /* ÏÇ¥Ïßù ÎàåÎ¶¨Îäî Ìö®Í≥º */
}

.result-divider {
  height: 3px;
  background-color: #d9d9d9;
  margin: 12px 0 16px;

  /* Ìå®Îî© Î¨¥ÏãúÌïòÍ≥† Î∂ÄÎ™® ÏòÅÏó≠ ÍΩâ Ï±ÑÏö∞Í∏∞ */
  margin-left: -16px;
  margin-right: -16px;
}
/* Î°úÎî© ÌÖçÏä§Ìä∏ */
.loading-text {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 180px; /* Í≥µÍ∞Ñ ÌôïÎ≥¥ */
  font-size: 15px;
  font-weight: 600;
  color: #008a69; /* Î©îÏù∏ Ïª¨Îü¨ */
  letter-spacing: 0.3px;
  animation: pulse 1.5s infinite;
}

@keyframes pulse {
  0% {
    opacity: 0.6;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0.6;
  }
}
