/* ===== iOS ÌÜ§ + ÏÉÅÎã® ÌûàÏñ¥Î°ú Í≥°ÏÑ† (Chatbot Overlay) ===== */
:root {
  --cb-max-w: 350px;
  --cb-max-h: 70svh;

  --bg: #f6f8fb;
  --panel: #ffffff;
  --border: #eceff3;

  --bot-bubble: #ffffff;
  --bot-bubble-border: #e7ebf0;

  --user-bubble: #37be91;
  --user-text: #ffffff;

  --brand: #008a69;
  --brand-dark: #006f54;

  --ink: #1f2937;
  --muted: #6b7280;

  --radius-panel: 26px;
  --radius-bubble: 22px;

  --pad: 14px;
  --btn-h: 46px;

  /* ‚¨áÔ∏è ÌûàÏñ¥Î°ú Ïù¥ÎØ∏ÏßÄ ÏÇ¨Ïù¥Ï¶à/Í≤πÏπ®(Ï†àÎ∞ò) */
  --hero-img: 85px;
  --hero-overlap: calc(var(--hero-img) * 0.5);
}

/* Root & Backdrop */
.cb-root {
  position: fixed;
  inset: 0;
  z-index: 1200;
  display: grid;
  place-items: center;
  font-family: var(--font);
}
.cb-backdrop {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.32);
}

/* Panel */
.cb-panel {
  position: relative;
  width: min(92vw, var(--cb-max-w));
  height: min(var(--cb-max-h), 640px);
  background: var(--panel);
  border-radius: var(--radius-panel);
  border: 1px solid var(--border);
  box-shadow: 0 10px 32px rgba(0, 0, 0, 0.18);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  animation: cb-pop 0.18s ease-out;
}
@keyframes cb-pop {
  from {
    transform: translateY(8px) scale(0.98);
    opacity: 0;
  }
  to {
    transform: translateY(0) scale(1);
    opacity: 1;
  }
}

/* Hero (image-only header with curved bottom) */
.cb-hero {
  position: relative;
  height: 100px; /* Í≥°ÏÑ† + Ïù¥ÎØ∏ÏßÄ ÏòÅÏó≠ ÌôïÎ≥¥ */
  display: grid;
  place-items: center;
  background: var(--bg);
}
.cb-hero::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -1px; /* Î≥∏Î¨∏Í≥º Ï†ïÌôïÌûà ÎßûÎãøÍ≤å */
  width: 120%;
  height: 170px; /* Í≥°ÏÑ† ÍπäÏù¥ */
  background: var(--bg); /* Î≥∏Î¨∏ Î∞∞Í≤ΩÏúºÎ°ú 'Ïò§Î™©Ìïú' Ìö®Í≥º */
  border-radius: 0 0 50% 50%;

  pointer-events: none;
  background: linear-gradient(#ffffff 100%);
  z-index: 1; /* Ïù¥ÎØ∏ÏßÄÎ≥¥Îã§ Îí§ */
}
.cb-hero-img {
  width: var(--hero-img);
  height: var(--hero-img);
  border-radius: 999px;
  object-fit: cover;
  background: #008a69;
  border: 4px solid #f1faf9;
  box-shadow: 0 8px 22px rgba(0, 0, 0, 0.08);

  /* ‚¨áÔ∏è Ï†àÎ∞ò Í≤πÏπòÎèÑÎ°ù ÏïÑÎûòÎ°ú ÎÇ¥Î¶¨Í≥†, Í≥°ÏÑ† ÏúÑÎ°ú Î≥¥Ïù¥Í≤å */
  transform: translateY(var(--hero-overlap));
  z-index: 2;
}

/* Glass back button (top-left) */
.cb-back2 {
  position: absolute;
  top: 20px;
  left: 20px;
  width: 36px;
  height: 36px;
  border-radius: 12px;
  border: 1px solid rgba(255, 255, 255, 0.06);
  background: rgba(255, 255, 255, 0.85);
  color: #6b7280;
  display: grid;
  place-items: center;
  cursor: pointer;
  backdrop-filter: blur(6px);
  -webkit-backdrop-filter: blur(6px);
  z-index: 3; /* Ïù¥ÎØ∏ÏßÄ/Í≥°ÏÑ† ÏúÑÏóê */
}

/* Body (messages area) */
.cb-body {
  position: relative;
  z-index: 0;
  flex: 1;
  overflow-y: auto;
  background: var(--bg);

  /* ‚¨áÔ∏è Í≤πÏπú ÎßåÌÅº ÏÉÅÎã® Ïó¨Î∞±ÏùÑ Ï∂îÍ∞ÄÌï¥ Î≤ÑÎ∏îÏù¥ Ïù¥ÎØ∏ÏßÄÎûë Í≤πÏπòÏßÄ ÏïäÍ≤å */
  padding: calc(var(--pad) + var(--hero-overlap)) var(--pad) var(--pad);
  /* ‚¨áÔ∏è Ïä§ÌÅ¨Î°§ Í∞úÏÑ† */
  -webkit-overflow-scrolling: touch; /* iOS Î∂ÄÎìúÎü¨Ïö¥ Ïä§ÌÅ¨Î°§ */
  scroll-behavior: smooth; /* Îß® ÏïÑÎûò Ïä§ÌÅ¨Î°§ ÏûêÏó∞Ïä§ÎüΩÍ≤å */
  overscroll-behavior: contain; /* ÏúÑ/ÏïÑÎûò Ìäê Î∞©ÏßÄ */
  /* ‚¨áÔ∏è Ïä§ÌÅ¨Î°§Î∞î Ïà®ÍπÄ */
  /* scrollbar-width: none; Firefox */
}
/* ===== Ïª§Ïä§ÌÖÄ Ïä§ÌÅ¨Î°§Î∞î (ÏùºÎ∞ò ÏÉâÏÉÅ) ===== */
.cb-body::-webkit-scrollbar {
  width: 6px; /* ÏñáÍ≤å */
}
.cb-body::-webkit-scrollbar-track {
  background: transparent; /* Î∞∞Í≤Ω Ìà¨Î™Ö */
}
.cb-body::-webkit-scrollbar-thumb {
  background: #cfcfcf; /* Ïó∞Ìïú ÌöåÏÉâ */
  border-radius: 999px; /* Îë•Í∏ÄÍ≤å */
}
.cb-body::-webkit-scrollbar-thumb:hover {
  background: #8c8c8c; /* hover Ïãú ÏßÑÌöåÏÉâ */
}
/* Messages */
.cb-messages {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.cb-row {
  display: flex;
  align-items: flex-end;
  gap: 8px;
  animation: cb-fadein 0.16s ease-out;
}
@keyframes cb-fadein {
  from {
    opacity: 0;
    transform: translateY(4px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.cb-row.bot {
  justify-content: flex-start;
}
.cb-row.user {
  justify-content: flex-end;
}

/* Bubbles */
.cb-bubble {
  max-width: 76%;
  padding: 10px 13px;
  font-size: 15px;
  line-height: 1.45;
  border-radius: var(--radius-bubble);
  word-break: break-word;
}
.cb-bubble.bot {
  background: var(--bot-bubble);
  color: var(--ink);
  border: 1px solid var(--bot-bubble-border);
  border-radius: 8px 18px 18px 18px;
}
.cb-bubble.user {
  background: var(--user-bubble);
  color: var(--user-text);
  border-radius: 18px 18px 8px 18px;
}

/* Input bar */
.cb-inputbar {
  display: grid;
  grid-template-columns: 1fr 46px;
  gap: 8px;
  padding: 10px var(--pad);
  background: var(--bg);
}
.cb-input {
  height: var(--btn-h);
  border: 1px solid var(--border);
  border-radius: 999px;
  padding: 0 14px;
  font-size: 15px;
  background: #fff;
}
.cb-input:focus {
  outline: none;
  border-color: #d8dee6;
}

/* Send button (round, paper plane) */
.cb-send-circle {
  height: var(--btn-h);
  width: var(--btn-h);
  border-radius: 999px;
  border: 0;
  background: var(--brand);
  color: #fff;
  display: grid;
  place-items: center;
  cursor: pointer;
  transition: transform 0.08s ease, background-color 0.2s ease,
    box-shadow 0.2s ease;
  box-shadow: 0 6px 16px rgba(46, 199, 201, 0.28);
}
.cb-send-circle:hover {
  background: var(--brand-dark);
}
.cb-send-circle:active {
  transform: scale(0.97);
}
.cb-send-circle:disabled {
  opacity: 0.6;
  cursor: default;
  box-shadow: none;
}
/* X Îã´Í∏∞ Î≤ÑÌäº */
.cb-close {
  position: absolute;
  top: 20px;
  right: 20px;
  width: 24px;
  height: 24px;
  background: none; /* Î∞∞Í≤Ω ÏóÜÏùå */
  border: none; /* ÌÖåÎëêÎ¶¨ ÏóÜÏùå */
  color: #444; /* ÏùÄÏùÄÌïú ÌöåÏÉâ (ÎÑàÎ¨¥ ÏßÑÌïòÏßÄ ÏïäÏùå) */
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 10;
  transition: opacity 0.2s ease, transform 0.1s ease;
}

.cb-close svg {
  stroke: currentColor;
  stroke-width: 1.5; /* ÏñáÏùÄ iOS ÎäêÎÇå */
}

.cb-close:hover {
  opacity: 0.7; /* Ìò∏Î≤Ñ Ïãú ÏùÄÏùÄÌïòÍ≤å */
}

.cb-close:active {
  transform: scale(0.9); /* ÎàåÎ†ÄÏùÑ ÎïåÎßå ÏÇ¥Ïßù Ï§ÑÏñ¥Îì¶ */
}
/* Î©îÏãúÏßÄ ÏãúÍ∞Ñ */
.cb-time {
  font-size: 11px;
  color: var(--muted);
  margin-top: 4px;
  text-align: right;
}
