:root {
  --sheet-radius: 16px;
  --sheet-max-h: 80dvh; /* JSÏóêÏÑú ÎçÆÏñ¥ÏîÄ */
  --sheet-min-h: 56svh; /* JSÏóêÏÑú ÎçÆÏñ¥ÏîÄ */
}

/* ‚úÖ ÌîÑÎ†àÏûÑ(.app) ÎÇ¥Î∂ÄÎßå ÎçÆÎèÑÎ°ù */
.sheet-host {
  position: absolute;
  inset: 0;
  pointer-events: none; /* backdropÏóêÏÑú Îã§Ïãú Ïº¨ */
  z-index: 1200;
}

.sheet-backdrop {
  position: absolute;
  inset: 0;
  display: grid;
  align-items: end; /* ÌïòÎã® Ï†ïÎ†¨ ‚Üí ÏïÑÎûòÏóêÏÑú Ïò¨ÎùºÏò§Îäî ÎäêÎÇå */
  justify-items: center;
  background: rgba(0, 0, 0, 0.45);
  opacity: 0; /* Î∞∞Í≤ΩÏùÄ ÌéòÏù¥ÎìúÎßå (Ïù¥Îèô X) */
  transition: opacity 0.24s ease-out;
  pointer-events: auto;
}

/* Ïó¥Î¶¥ ÎïåÎßå Î∂ÄÎìúÎüΩÍ≤å ÌéòÏù¥Îìú Ïù∏ */
.sheet-host.open .sheet-backdrop {
  opacity: 1;
}

/* === ÏãúÌä∏ === */
.sheet {
  width: 100%;
  background: #fff;
  border-top-left-radius: var(--sheet-radius);
  border-top-right-radius: var(--sheet-radius);
  box-shadow: 0 -10px 30px rgba(0, 0, 0, 0.25);

  /* ÏïÑÎûòÏóêÏÑú ÏúÑÎ°ú: transformÎßå Ïï†ÎãàÎ©îÏù¥ÏÖò(Î†àÏù¥ÏïÑÏõÉ ÏòÅÌñ• ÏóÜÏùå) */
  transform: translate3d(0, 100%, 0);
  transform-origin: bottom center;
  transition: transform 0.38s cubic-bezier(0.22, 1, 0.36, 1);
  backface-visibility: hidden;
  will-change: transform;

  max-height: var(--sheet-max-h);
  min-height: var(--sheet-min-h);
  display: flex;
  flex-direction: column;
  overflow: hidden;

  padding-bottom: max(12px, env(safe-area-inset-bottom));
}

/* Ïó¥Î¶º ÏÉÅÌÉú */
.sheet.open {
  transform: translate3d(0, 0, 0);
}

.sheet-handle {
  width: 44px;
  height: 5px;
  border-radius: 999px;
  background: #e5e7eb;
  margin: 8px auto 4px;
}

.sheet-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 16px;
  border-bottom: 1px solid #f0f0f0;
}
.sheet-title {
  margin: 0;
  font-size: 16px;
  font-weight: 800;
}
.sheet-close {
  border: 0;
  background: transparent;
  cursor: pointer;
  font-size: 20px;
  line-height: 1;
  padding: 4px;
}

.sheet-body {
  padding: 16px;
  overflow: auto;
  overscroll-behavior: contain; /* Î∞∞Í≤ΩÏúºÎ°ú Ïä§ÌÅ¨Î°§ Ï†ÑÌåå Î∞©ÏßÄ */
  -ms-overflow-style: none; /* (ÏÑ†ÌÉù) ÎÇ¥Î∂Ä Ïä§ÌÅ¨Î°§Î∞î Ïà®ÍπÄ */
  scrollbar-width: none;
}
.sheet-body::-webkit-scrollbar {
  width: 0;
  height: 0;
}

/* Ï†ëÍ∑ºÏÑ±: Î™®ÏÖò ÏµúÏÜåÌôî ÌôòÍ≤Ω */
@media (prefers-reduced-motion: reduce) {
  .sheet-backdrop {
    transition: opacity 0.001s linear;
  }
  .sheet {
    transition: transform 0.001s linear;
  }
}
