  height: 200px; /* ‚úÖ ÏõêÌïòÎäî Í≥†Ï†ï ÎÜíÏù¥ */
  padding: 8px;
  box-sizing: border-box;
}

/* Ïä§ÌÅ¨Î°§ Î™®Îìú */
.meal-items-area.scroll-mode {
  overflow-y: auto;
}

/* ÏÑ∏Î°ú Í∞ÄÏö¥Îç∞ Î™®Îìú */
.meal-items-area.center-mode {
  display: flex;
  flex-direction: column;
  justify-content: center; /* ‚úÖ ÏÑ∏Î°ú Í∞ÄÏö¥Îç∞ */
  align-items: stretch; /* Í∞ÄÎ°úÎèÑ Ï†ïÏ§ëÏïô */
}

/* Ïä§ÌÅ¨Î°§Î∞î Ïä§ÌÉÄÏùº */
.meal-items-area.scroll-mode::-webkit-scrollbar {
  width: 3px;
}
.meal-items-area.scroll-mode::-webkit-scrollbar-thumb {
  background: #cbd5e1;
  border-radius: 10px;
}
.meal-items-area.scroll-mode::-webkit-scrollbar-track {
