/* ÌôîÎ©¥ Ï†ÑÏ≤¥ Ï§ëÏïô Î∞∞Ïπò */
.app-wrap {
  height: 100dvh;
  display: grid;
  place-items: center;
  background: #ececec;
}

/* Ïï± Î∞ïÏä§ */
.app {
  width: 390px;
  height: min(100dvh, 800px);
  max-height: none;
  background: #f9fafe;
  border-radius: 29px;
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: column;
}

.content-scroll {
  min-height: 0;
  flex: 1;
  overflow-y: auto;
  /* overflow-y: overlay; */
  background: #f9fafe;
  overflow-x: hidden; /* ‚úÖ Î∞îÍπ• Î†àÏù¥ÏïÑÏõÉÏóê Í∞ÄÎ°ú Ïä§ÌÅ¨Î°§ Í∏àÏßÄ */
  scrollbar-gutter: stable overlay;
  /* padding-bottom: var(--footer-h); ‚úÖ footer Ïïà Í∞ÄÎ¶¨Í≤å */
}

/* Ïä§ÌÅ¨Î°§Î∞î ÎîîÏûêÏù∏ */
.content-scroll::-webkit-scrollbar {
  width: 4px;
}

.content-scroll::-webkit-scrollbar-track {
  background: transparent;
}

.content-scroll::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 4px;
}

.content-scroll::-webkit-scrollbar-thumb:hover {
  background-color: rgba(0, 0, 0, 0.4);
}
.footer {
  flex-shrink: 0; /* footer ÎÜíÏù¥ Í≥†Ï†ï */
}
