/* ===== Reset ===== */
*,
*::before,
*::after {
  box-sizing: border-box;
}
html,
body {
  height: 100%;
  margin: 0;
}

/* ===== Ïπ¥Îìú(Ïï± ÌãÄ) 3Ìñâ Í∑∏Î¶¨Îìú =====
   [Ìó§Îçî(52px)] [Î≥∏Î¨∏(Ïä§ÌÅ¨Î°§)] [Ìë∏ÌÑ∞(Í≥†Ï†ï)]
*/
.surveyContainer {
  font-family: "Noto Sans KR", sans-serif;
  color: #000;

  width: 100%;
  max-width: clamp(360px, 92vw, 500px);
  height: 100%; /* Ïπ¥Îìú ÎÜíÏù¥ = ÌôîÎ©¥ ÎÜíÏù¥ */
  margin: 0 auto;
  background: #fff;

  display: grid;
  grid-template-rows: 52px 1fr auto; /* Ìó§Îçî / Î≥∏Î¨∏ / Ìë∏ÌÑ∞ */
  overflow: hidden; /* ÎùºÏö¥Îìú/Í≤ΩÍ≥Ñ Î∞ñÏúºÎ°ú Ïïà ÏÉàÍ≤å */
}

/* ===== Ìó§Îçî(1Ìñâ) ===== */
.headerPlaceholder {
  height: 52px; /* grid Ï≤´ ÌñâÍ≥º ÎèôÏùº */
}

/* ===== Î≥∏Î¨∏(2Ìñâ): Ïù¥ ÏòÅÏó≠Îßå Ïä§ÌÅ¨Î°§ ===== */
.contentWrap {
  min-height: 0; /* Í∑∏Î¶¨Îìú ÏûêÏãù Ïä§ÌÅ¨Î°§ ÌïÑÏàò */
  overflow-y: auto; /* Î≥∏Î¨∏Îßå Ïä§ÌÅ¨Î°§ */
  padding: 16px 20px; /* ‚¨ÖÔ∏è ÌïòÎã® Ìå®Îî© Ï£ºÏßÄ Îßà! Ìë∏ÌÑ∞Í∞Ä Îî∞Î°ú ÏûàÏùå */
  text-align: left;
  padding-bottom: 0 !important;
}

/* ===== Ìë∏ÌÑ∞(3Ìñâ): Ïπ¥Îìú ÌïòÎã® Í≥†Ï†ï, Ïä§ÌÅ¨Î°§ÏóêÎèÑ Í∞ÄÎßåÌûà ===== */
.bottomBar {
  position: static; /* fixed/sticky Î∂àÌïÑÏöî ‚Äî Í∑∏Î¶¨Îìú ÎßàÏßÄÎßâ ÌñâÏù¥Îùº Ìï≠ÏÉÅ ÌïòÎã® */
  background: #ffffff;
  border-top: 1px solid #eee;
  padding: 16px 20px;
  z-index: 1;
}

/* ===== Î≤ÑÌäº Ï†ïÎ†¨ ===== */
.bottomInner {
  display: flex;
  gap: 12px;
  align-items: center;
  justify-content: space-between;
}

/* ===== ÌéòÏù¥ÏßÄ ÌëúÏãú ===== */
.pageIndicator {
  margin: 0 0 16px;
  font-weight: 800;
  font-size: 20px;
  color: #ff8000;
  text-align: left;
}

/* ===== ÏïàÎÇ¥ / ÌÉÄÏù¥ÌãÄ / ÏßàÎ¨∏ ===== */
.guideText {
  font-size: 20px;
  font-weight: 600;
  line-height: 1.6;
  margin: 0 0 20px;
  color: #000;
}
.title {
  font-size: 17px;
  font-weight: 700;
  line-height: 1.5;
  margin: 0 0 8px;
}
.question {
  font-size: 14px;
  font-weight: 500;
  margin: 14px 0 6px;
  padding-right: 8px;
}
.desc {
  font-size: 12px;
  color: #7c7c7c;
  margin: 0 0 16px;
}
.helperText {
  font-size: 13px;
  color: #7c7c7c;
  font-weight: 400;
  line-height: 1.5;
  margin: 2px 0 8px;
}

/* ===== ÏÑπÏÖò/ÎùºÎ≤® ===== */
.section {
  margin-bottom: 20px;
}
.section label {
  display: block;
  margin-bottom: 8px;
  font-size: 13px;
  font-weight: 600;
  color: #7c7c7c;
}

/* ===== Ìèº Í≥µÌÜµ ===== */
input,
select,
textarea,
button {
  font-family: inherit;
  font-size: 14px;
  line-height: 1.2;
}
input[type="number"]::-webkit-outer-spin-button,
input[type="number"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
input[type="number"] {
  appearance: textfield;
}

/* ===== ÏûÖÎ†• ÌïÑÎìú ===== */
.inputField {
  width: 100%;
  height: 46px;
  border-radius: 12px;
  border: 1.5px solid #c7c7c7;
  outline: none;
  font-size: 14px;
  padding: 0 14px;
  transition: border-color 0.15s ease, box-shadow 0.15s ease;
}
.inputField:focus {
  border-color: #008a69;
  box-shadow: 0 0 0 4px rgba(0, 138, 105, 0.15);
}

/* ===== ÏÑ†ÌÉù Î≤ÑÌäº ===== */
.optionsGrid {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 12px 16px;
  margin: 20px 0;
}
.optionBtn {
  min-width: 96px;
  height: 42px;
  padding: 0 16px;
  border-radius: 22px;
  border: 1.5px solid #c7c7c7;
  background: #ffffff;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.15s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  white-space: nowrap;
}
.optionBtn:hover {
  border-color: #7c7c7c;
}
.selected {
  border-color: #008a69;
  background: #baf8e9;
  color: #008a69;
  font-weight: 700;
}

/* ===== ÏóêÎü¨ ===== */
.error {
  margin-top: 6px;
  font-size: 12px;
  color: #dc2626;
}
.invalid {
  border-color: #dc2626 !important;
}

/* ===== CTA Î≤ÑÌäº ===== */
.prevBtn,
.nextBtn {
  width: 50%;
  height: 50px;
  border-radius: 999px;
  font-weight: 800;
  font-size: 16px;
  cursor: pointer;
  border: 2px solid transparent;
  padding: 0 28px;
  margin: 0; /* Ìë∏ÌÑ∞ ÎÜíÏù¥ ÎäòÏßÄ ÏïäÍ≤å 0 */
}
.prevBtn {
  background: #fff;
  color: #008a69;
  border-color: #008a69;
}
.nextBtn {
  background: #008a69;
  color: #fff;
  border-color: #008a69;
}
.nextBtn:disabled {
  background: #c7c7c7;
  border-color: #c7c7c7;
  cursor: not-allowed;
}

/* ===== Î≥¥Ï°∞ Pill ===== */
.infoPill {
  display: inline-block;
  padding: 4px 10px;
  margin: 4px 0 10px;
  border-radius: 999px;
  background: #eafbef;
  color: #10b981;
  font-weight: 700;
  font-size: 12px;
}
