.ingredients-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px 30px;
  padding-bottom: 30px;
}

.ingredient-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 17px;
  color: #333;
  padding: 8px 0;
  border-bottom: 1px solid #f5f5f5;
}

.ingredient-left {
  display: flex;
  align-items: center;
  gap: 10px;
}

/* Í∏∞Î≥∏ Ï≤¥ÌÅ¨Î∞ïÏä§ Ïà®Í∏∞Í≥† ÎÑ§Î™® Î∞ïÏä§ Ïä§ÌÉÄÏùº ÎßåÎì§Í∏∞ */
.ingredient-left input[type="checkbox"] {
  appearance: none; /* Î∏åÎùºÏö∞Ï†Ä Í∏∞Î≥∏ Ïä§ÌÉÄÏùº Ï†úÍ±∞ */
  -webkit-appearance: none; /* ÏÇ¨ÌååÎ¶¨ ÎåÄÏùë */
  width: 19px;
  height: 19px;
  border: 2px solid #ccc;
  border-radius: 4px; /* ÏÇ¥Ïßù Îë•Í∑º Î™®ÏÑúÎ¶¨ */
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: all 0.2s ease;
}

/* Ï≤¥ÌÅ¨ÎêêÏùÑ Îïå */
.ingredient-left input[type="checkbox"]:checked {
  background-color: #76a37c; /* ‚úÖ Ï¥àÎ°ùÏÉâ Î∞∞Í≤Ω */
  border-color: #76a37c;
}

/* Ï≤¥ÌÅ¨ ÌëúÏãú (‚úì) */
.ingredient-left input[type="checkbox"]:checked::after {
  content: "‚úì";
  color: white;
  font-size: 13px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
}

.ingredient-amount {
  font-weight: 500;
  color: #444;
}
