/* ÏïΩÍ¥Ä ÎèôÏùò Î¶¨Ïä§Ìä∏ */
.agreement-list {
  list-style: none;
  margin: 0;
  padding: 0; /* ‚úÖ Ïó¨Î∞± Ï†úÍ±∞ */
  display: flex;
  flex-direction: column;
  margin-top: 160px;
  gap: 10px;
}

/* Ï†ÑÏ≤¥ ÎèôÏùò Ìï≠Î™© */
.agreement-list li:first-child {
  border-bottom: 1px solid #ddd;
  padding: 12px 0; /* ÏúÑÏïÑÎûòÎßå Í∞ÑÍ≤© */
  margin: 0; /* ‚úÖ margin Ï†úÍ±∞ */
}

/* ÎÇòÎ®∏ÏßÄ Ìï≠Î™© */
.agreement-list li:not(:first-child) {
  padding: 12px 0; /* ÎèôÏùºÌïú ÏÑ∏Î°ú Í∞ÑÍ≤© */
}

.agreement-list label {
  display: flex;
  align-items: center; /* input Í≥º ÌÖçÏä§Ìä∏Î•º ÏàòÏßÅ Ï§ëÏïô Ï†ïÎ†¨ */
  gap: 8px; /* inputÍ≥º ÌÖçÏä§Ìä∏ Í∞ÑÍ≤© */
  cursor: pointer;
}

/* Ï≤¥ÌÅ¨Î∞ïÏä§ Í∏∞Î≥∏ */
.agreement-list input[type="checkbox"] {
  appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid #008a69;
  border-radius: 4px;
  margin-right: 10px;
  cursor: pointer;
  position: relative;
  flex-shrink: 0;
  background: #fff;
}

/* Ï≤¥ÌÅ¨Îêú ÏÉÅÌÉú */
.agreement-list input[type="checkbox"]:checked {
  background-color: #008a69;
  border-color: #008a69;
}

/* Ï≤¥ÌÅ¨ ÏïÑÏù¥ÏΩòÏùÑ CSSÎ°ú */
.agreement-list input[type="checkbox"]:checked::after {
  content: "";
  position: absolute;
  left: 5px;
  /* top: 1px; */
  width: 6px;
  height: 12px;
  border: solid #fff;
  border-width: 0 2px 2px 0;
  transform: rotate(45deg);
}

/* Î≤ÑÌäº */
.next-btn {
  width: 100%;
  padding: 14px;
  font-size: 16px;
  font-weight: 700;
  border: none;
  border-radius: 10px;
  background-color: #008a69;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.25s ease;
}
.next-btn:hover:enabled {
  background-color: #007356;
}
.next-btn:disabled {
  background-color: #b5dcd2;
  cursor: not-allowed;
}
